@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;500&family=Roboto:wght@100;300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.about {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #141B28;
  padding: 80px 10px 80px 10px;
}

#about-header {
  color: white;
}

#about-info {
  margin: 10px;
  width: 50%;
  text-align: center;
  color: white;
}

#logo1, #logo2, #logo3, #logo4 {
  height: 150px;
  width: 300px;
  transition: all 0.3s;
}

#logo1:hover, #logo2:hover, #logo3:hover, #logo4:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.projects {
  background-color: white;
  padding-top: 80px;
  padding-bottom: 80px;
  color: #141B28;
}

.project-div {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

#proj1, #proj2, #proj3, #proj4 {
  flex: 1 1;
  margin: 10px;
  max-width: 350px;
}

#projects-header {
  margin: 0px;
  padding-top: 40px;
  color: #141B28;
}

.project-stack {
  font-weight: 500;
}

.skills {
  padding-top: 80px;
  padding-bottom: 80px;
  background-color: #37676E;
  color: white;
}

.skills-div {
  display: flex;
  flex-direction: row;
}

#languages, #management, #methods, #databases {
  flex: 1 1;
  margin: 10px;
}

#github, #linkedin {
  text-decoration: none;
  color: white;
  margin: 10px;
}

.contact {
  padding-top: 80px;
  padding-bottom: 80px;
  background-color: #163559;
  color: white;
}

@media only screen and (max-width: 450px) {
  #about-info {
    width: 90%;
    margin: 5px;
  }
  .skills-div {
    flex-direction: column;
  }
}
.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-bottom: 60px;
  background-color: #141B28;
  /* position: fixed; */
}

.nav {
  display: flex;
  flex-direction: row;
}

#name {
  font-family: "Montserrat", sans-serif;
  margin-left: 40px;
  color: white;
}

.nav-item {
  margin: 20px 10px 20px 10px;
  color: white;
  text-decoration: none;
  font-size: 18px;
}

@media only screen and (max-width: 450px) {
  #name {
    font-size: 24px;
  }
  .nav-item {
    font-size: 12px;
    margin: 2px;
    padding: 2px;
  }
  .nav {
    flex-direction: column;
    padding-top: 10px;
  }
}
.footer {
  background-color: #163559;
  padding-bottom: 4px;
}

#footer-text {
  /* padding: 2px; */
  color: white;
  font-size: 12px;
  text-decoration: none;
}
.layout {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

main {
  flex: 1 1;
  /* background-color: #2c9ad5; */
  /* padding-bottom: 40px; */
}
.App {
  text-align: center;
  font-family: 'Roboto', sans-serif;
}

